import zxcvbn from 'zxcvbn';

export function checkPasswordStrength(password) {
  const result = zxcvbn(password);

  let color = ''; //    

  switch (result.score) {
    case 0:
      color = 'red'; //   
      break;
    case 1:
      color = 'orange'; //  
      break;
    case 2:
      color = 'yellow'; //  
      break;
    case 3:
      color = 'green'; //  
      break;
    case 4:
      color = 'darkgreen'; //   
      break;
    default:
      color = ''; //     
      break;
  }

  return {
    score: result.score,
    color,
  };
}
